import React, { useEffect, useState } from 'react';
import { useSelector, useDispatch } from 'react-redux';
import { Container, Row, Col, Card } from 'react-bootstrap';
import { fetchCountries } from '../features/countrySlice';  
import Header from '../components/Header';  
import Footer from '../components/Footer';  

const HomePage = () => {
  const dispatch = useDispatch();
  const { countries, status, error } = useSelector((state) => state.countries);
  const [filter, setFilter] = useState('All');

  useEffect(() => {
    dispatch(fetchCountries());
  }, [dispatch]);

  const handleFilterChange = (region) => {
    setFilter(region);
  };

  const filteredCountries = countries.filter(country => 
    filter === 'All' || country.region === filter
  );

  if (status === 'loading') {
    return <div>Loading...</div>;
  }

  if (status === 'failed') {
    return <div>Error: {error}</div>;
  }

  return (
    <>
      <Header onFilterChange={handleFilterChange} /> 
      <Container className="mt-4">
        <div className="scrollable-container" style={{ maxHeight: '500px', overflowY: 'scroll' }}>
          <Row xs={1} md={2} lg={2} className="g-4">
            {filteredCountries.map((country, index) => (
              <Col key={index} className="row-height">
                <Card className="h-100">
                  <div className="d-flex align-items-center p-3">  
                    <div className="flex-shrink-0">
                      <Card.Img 
                        src={country.flag} 
                        className="img-fluid card-image" 
                        alt={`Flag of ${country.name}`}
                      />
                    </div>
                    <div className="flex-grow-1 ms-3">
                      <Card.Body>
                        <Card.Title>{country.name}</Card.Title>
                        <Card.Text>{country.region}</Card.Text>
                      </Card.Body>
                    </div>
                  </div>
                </Card>
              </Col>
            ))}
          </Row>
        </div>
        <div className="mt-5 text-center copyright">copyright @ 2024 All rights reserved</div>
      </Container>
      <Footer />
    </>
  );
};

export default HomePage;
